@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,300,600,700,800,300italic,400italic,600italic,700italic,800italic);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1,h2,h3,h4,h5{font-family:"Montserrat",sans-serif;color:#e7e7e7;margin:0px}h1{font-size:36px;font-weight:400}h3{font-size:14px;font-weight:400;color:#fe4141}h5{font-size:12px;font-weight:400}.movie-gen,.movie-likes{margin:0px;padding:0px}.movie-gen li,.movie-likes li{font-family:"Open Sans",sans-serif;font-size:12px;color:#818181;width:auto;display:block;float:left;margin-right:6px;font-weight:600}.movie-likes{float:right}.movie-likes li{color:#fe4141}.movie-likes li:last-child{margin-right:0px}.movie-likes li i{font-size:14px;margin-right:4px;position:relative;float:left;line-height:1}.movie-details{font-family:"Open Sans",sans-serif;font-size:12px;font-weight:300;color:#b4b4b4}.movie-description{font-family:"Open Sans",sans-serif;font-size:12px;font-weight:400;color:#9b9b9b;text-align:justify;line-height:1.3}.movie-actors{font-family:"Open Sans",sans-serif;font-size:12px;font-weight:300;color:#e7e7e7;font-style:italic}.swiper-slide-prev .cellphone-container{display:none}.button-container{width:100%;margin:20px auto;display:flex;justify-content:space-between}.container{width:100%;height:100%}.cellphone-container{width:100%;height:650px;background-color:#1e1b26;margin:60px auto 0 auto;box-shadow:5px 5px 115px -14px black;border-radius:4px}.movie-img{width:100%;height:380px;background-position:center;background-repeat:no-repeat;background-size:cover;z-index:111 !important;border-top-left-radius:4px;border-top-right-radius:4px;position:relative}.text-movie-cont{padding:0px 12px;text-align:justify}.text-movie-cont h1{font-size:18px;margin:16px 0}.text-movie-cont h5{font-size:16px}.summary-row{margin-top:12px}@media only screen and (max-width: 500px){.cellphone-container{margin:0 auto}}.thegrid{margin:0 auto}.elements-distance,.movie-details,.movie-description,.movie-actors{margin:0px}.mr-grid{width:100%}.mr-grid:before,.mr-grid:after{content:"";display:table}.mr-grid:after{clear:both}.mr-grid{*zoom:1}.col1,.col2,.col3,.col4,.col5,.col6{margin:0% 0.5% 0.5% 0.5%;padding:1%;float:left;display:block}.col1{width:98%}.col2{width:47%}.col3{width:30.3333333333%}.col4{width:22%}.col5{width:17%}.col6{width:13.6666666667%}body{background:linear-gradient(rgba(30,27,38,0.95), rgba(30,27,38,0.95)),url("https://www.learner.org/wp-content/uploads/2019/01/American-Cinema-main-fig1.jpg");background-position:center;background-size:cover;background-repeat:repeat}

